.ornaments-container {
	position: relative;
	@include max-md {
		padding-left: 0 !important;
		padding-right: 0 !important;
	}
	.ornaments {
		position: relative;
		width: 200%;
		height: 280px;
		display: flex;
		float: right;
		.ornaments-content {
			width: 100%;
			height: 100%;
			@include min-lg {
				width: 50%;
			}
			@include max-lg {
				&:nth-child(3),
				&:nth-child(4) {
					display: none;
				}
			}
			.ornament-1 {
				position: absolute;
				top: 0;
				left: 23%;
				width: 100px;
				@include min-md {
					width: 120px;
				}
				@include min-lg {
					width: 140px;
				}
				img {
					transform-origin: 0% 30%;
				}
			}
			.ornament-2 {
				position: absolute;
				top: 0;
				right: 8%;
				width: 100px;
				@include min-md {
					width: 120px;
				}
			}
			.ornament-3 {
				position: absolute;
				top: 30%;
				left: 0;
				width: 60px;
				@include min-md {
					width: 80px;
				}
				img {
					transform-origin: 0% 30%;
				}
			}
			.ornament-4 {
				position: absolute;
				bottom: 0;
				left: 8%;
				width: 100px;
				// transform: translateX(-10%);
				@include min-md {
					width: 120px;
				}
			}
			.ornament-5 {
				position: absolute;
				top: 60%;
				left: 50%;
				width: 100px;
				transform: translate(-50%, -50%);
				@include min-md {
					width: 120px;
				}
				img {
					transform-origin: 0% 30%;
				}
			}
			.ornament-6 {
				position: absolute;
				top: 50%;
				right: 0;
				width: 80px;
				@include min-md {
					width: 100px;
				}
				img {
					transform-origin: 0% 30%;
				}
			}
			.ornament-7 {
				position: absolute;
				bottom: 0;
				left: 60%;
				width: 60px;
				@include min-md {
					width: 80px;
				}
				img {
					transform-origin: 0% 30%;
				}
			}
			.ornament-8 {
				position: absolute;
				top: 60%;
				left: 50%;
				width: 80px;
				transform: translate(-50%, -50%);
				@include min-md {
					width: 100px;
				}
				img {
					transform-origin: 0% 30%;
				}
			}
			.ornament-9 {
				position: absolute;
				top: 0;
				right: 0;
				width: 60px;
				transform: translateX(60%);
				@include min-md {
					width: 80px;
				}
			}
		}
	}
}

img[alt="lotus-1"],
img[alt="lotus-2"] {
	transform-origin: 0% 30%;
}
