.reminder-wrap {
	position: relative;
	display: block;
	padding: 5rem 0 0;
	@include max-md {
		padding: 3rem 0 0;
	}
	.orn-4 {
		position: relative;
		width: 120px;
		margin: 0 auto 1rem;
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
		path {
			fill: var(--color-button-trans);
			stroke: var(--color-button-trans);
		}
	}
	.reminder-content {
		position: relative;
		width: fit-content;
		max-width: 100%;
		z-index: 2;
		margin: 0 auto;
		border: 2px solid var(--color-button-trans);
		border-radius: 4px;
		padding: 1rem;
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
