.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 30%;
		left: 0;
		width: 40px;
		transform: translate(-100%, -50%);
		@include min-md {
			width: 60px;
			transform: translate(-150%, -50%);
		}
		@include min-lg {
			width: 70px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 50px;
		transform: translate(100%, -100%);
		@include min-md {
			width: 70px;
			transform: translate(120%, -100%);
		}
		@include min-lg {
			width: 80px;
			transform: translate(150%, -100%);
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 40px;
		@include min-md {
			width: 60px;
			transform: translateX(50%);
		}
		@include min-lg {
			right: -1rem;
		}
	}
	.image-wrapper {
		position: relative;
		width: 90%;
		height: auto;
		margin: 0 auto;
		.image-element {
			position: relative;
			.thank-background {
				width: 100%;
				height: auto;
				object-fit: cover;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 1rem 5rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
