.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin: 1rem 0;
		z-index: 3;
		span {
			position: relative;
			font-size: 24px;
			font-family: $font-secondary;
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		right: 0;
		width: 40px;
		transform: translate(-70%, -100%);
		@include min-md {
			right: 20%;
		}
		@include min-lg {
			width: 60px;
			right: 25%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 20%;
		left: 0;
		width: 60px;
		@include min-md {
			width: 80px;
		}
		@include min-lg {
			width: 120px;
		}
	}
	.orn-3 {
		position: absolute;
		top: 40%;
		right: 5px;
		width: 40px;
		@include min-md {
			width: 60px;
		}
		@include min-lg {
			width: 80px;
		}
	}
	.couple-wrapper {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
		margin-bottom: 3rem;
	}
	.couple {
		position: relative;
		text-align: center;
		@include max-md {
			display: flex;
			align-items: center;
			&.man {
				text-align: left;
				.couple-description {
					padding: 0 0 0 0.75rem;
				}
			}
			&.woman {
				text-align: right;
				flex-direction: row-reverse;
				.couple-description {
					padding: 0 0.75rem 0 0;
				}
			}
		}
		.image-wrap {
			width: 80%;
			position: relative;
			aspect-ratio: 1;
			margin: 0 auto;
			@include max-md {
				width: 40%;
				margin: 0;
			}
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					border-radius: 300px;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem;
			@include max-md {
				width: 60%;
			}
			h3 {
				font-weight: 700;
				@include max-md {
					font-size: 18px;
				}
			}
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			margin: 1rem auto 0;
			.sosmed {
				color: var(--text-button);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
