.countdown {
	display: grid;
	grid-template-columns: 1fr 1fr;
	justify-content: center;
	position: relative;
	margin: 0 auto;
	width: fit-content;
	gap: 0.75rem;
	padding: 0 1rem;
	z-index: 3;
	border: 2px solid var(--color-button-trans);
	border-radius: 4px;
	@include max-md {
		width: 100%;
	}
	.days,
	.hours,
	.minutes,
	.seconds {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		position: relative;
		padding: 1rem;
		min-width: 80px;
		@include max-md {
			min-width: 65px;
			padding: 1rem 0.75rem;
		}
		@include max-xs {
			min-width: 63px;
		}
		@include max-rwd(350px) {
			min-width: 72px;
		}
		.huruf {
			font-size: 14px;
			font-weight: 600;
			@include max-xs {
				font-size: 12px;
			}
		}
		.angka {
			margin-bottom: 0;
			font-weight: 600;
			font-size: 38px;
			font-family: $font-secondary;
			font-style: italic;
			@include max-md {
				font-size: 28px;
			}
			@include max-xs {
				font-size: 24px;
			}
		}
		.huruf,
		.angka {
			margin: 0;
			padding: 0;
		}
	}
}
