.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 5%;
		left: 5%;
		width: 40px;
		@include min-md {
			left: 10%;
			width: 60px;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 5%;
		right: 0;
		width: 60px;
		transform: translateX(20%);
		@include min-md {
			width: 80px;
		}
		@include min-lg {
			right: 10%;
			width: 100px;
		}
	}
	.appology-wrap {
		position: relative;
	}
}
