.header-section {
	position: relative;
	width: 100%;
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
	}
	.orn-1 {
		position: absolute;
		top: 25%;
		left: 0;
		width: 100px;
		@include min-md {
			left: 5%;
			width: 120px;
		}
		@include min-lg {
			left: 15%;
			width: 140px;
		}
		path {
			fill: var(--color-button-trans);
		}
	}
	.orn-2 {
		position: absolute;
		top: 30%;
		right: 0;
		width: 40px;
		@include min-md {
			right: 12%;
			width: 60px;
		}
		@include min-lg {
			right: 18%;
		}
		path {
			fill: var(--color-button-trans);
		}
	}
	.orn-3 {
		position: absolute;
		top: 75%;
		left: 0;
		width: 40px;
		@include min-md {
			left: 16%;
			width: 60px;
		}
		@include min-lg {
			left: 20%;
			width: 80px;
		}
		path {
			fill: var(--color-button-trans);
		}
	}
	.header-content {
		position: relative;
		min-height: 100vh;
		.header {
			position: relative;
			display: grid;
			grid-template-rows: auto 1fr;
			width: 40%;
			height: 100%;
			margin: 0 auto;
			padding: 4.5rem 0 0;
			@include max-xl {
				width: 50%;
			}
			@include max-lg {
				width: 60%;
			}
			@include max-md {
				width: 100%;
			}
			.header-title {
				position: relative;
				text-align: center;
				&-content {
					position: relative;
					.title {
						text-align: right;
						width: fit-content;
						max-width: 100%;
						margin: 1rem auto;
					}
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
			.image-wrapper {
				position: relative;
				width: 70%;
				height: auto;
				aspect-ratio: 3/4;
				margin: 0 auto 0;
				padding-top: 1rem;
				@include max-lg {
					width: 100%;
				}
				@include max-md {
					width: 80%;
				}
				.header-background {
					width: 100%;
					height: auto;
				}
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
