.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	width: 100%;
	&.cover-opened {
		transform: translateY(-100%);
	}
	.cover-wrapper {
		position: relative;
		padding: 0 0 3rem;
		transition: opacity 1s ease;
		height: 100vh;
		@include max-lg {
			padding: 0 0 6.5rem;
			align-items: center;
		}
		.image-wrapper {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 80%;
			@include max-xxl {
				height: 70%;
			}
			@include max-rwd(410px) {
				height: 60%;
			}
			@include max-rwd(380px) {
				height: 50%;
			}
			.cover-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				position: relative;
				mask-image: url(../images/mask-cover-desktop.png);
				mask-size: 100% 100%;
				mask-repeat: no-repeat;
				@include max-lg {
					mask-image: url(../images/mask-cover-mobile.png);
				}
			}
		}
		.cover-content {
			position: relative;
			width: 100%;
			height: 100%;
			display: flex;
			align-items: flex-end;
			justify-content: center;
			.cover-body {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				.greeting-wrapper {
					position: relative;
					@include max-md {
						font-size: 14px;
					}
					h6 {
						font-weight: 700;
					}
				}
				.cover-description {
					position: relative;
					text-align: center;
					.title {
						text-align: right;
						width: fit-content;
						max-width: 100%;
						margin: 1rem auto;
					}
					.cover-event {
						position: relative;
						h6 {
							font-weight: 700;
						}
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
