.stream-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.ornaments-container {
		padding-top: 1rem 0;
	}
	.title-section {
		.line {
			width: 80%;
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 30px;
		@include min-md {
			width: 50px;
		}
		@include min-lg {
			left: 20%;
			width: 60px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 2rem;
		right: 0;
		width: 50px;
		transform: translateX(15%);
		@include min-md {
			width: 70px;
		}
		@include min-lg {
			right: 18%;
			width: 80px;
		}
	}
	.stream-wrapper {
		position: relative;
		.stream-content {
			position: relative;
			.stream-description {
				position: relative;
				border: 2px solid var(--color-button-trans);
				border-radius: 4px;
				padding: 3rem 2rem;
				@include max-md {
					padding: 2rem 1rem;
				}
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		position: relative;
		width: 100%;
	}
}
